<template>
  <div class="container">
    <h3 class="heading">الوحدات ({{ sections.length }})</h3>
    <br />
    <div class="row" dir="rtl">
      <div class="col-md-4">
        <label>اختر السنة الدراسية</label>
        <select class="form-control" v-model="stage">
          <option selected value="1">الصف الأول الاعدادي</option>
          <option selected value="2">الصف الثاني الاعدادي</option>
          <option selected value="3">الصف الثالث الاعدادي</option>
          <option selected value="4">الصف الأول الثانوي</option>
          <option selected value="5">الصف الثاني الثانوي</option>
          <option selected value="6">الصف الثالث الثانوي</option>
        </select>
      </div>
    </div>
    <!------------------------------------------------->
    <!-----------["title", "items", "loading", "colsTitles", "colsTypes", "btns", "color"],--------------->

    <div class="">
      <responsive-table
        title=""
        :items="sections"
        :cols="['number', 'name', 'stage', 'price', 'orderedTasks']"
        :colsTitles="['رقم', 'العنوان', 'المرحلة', 'السعر', 'واجبات مرتبة']"
        :colsTypes="['readonly', 'readonly', 'readonly', 'readonly', 'readonly']"
        :loading="loading"
        color="#222f22"
        :btns="[
          {
            icon: 'pencil',
            method: (section) => $router.push('editSection/' + section._id),
          },
          { icon: 'trash', method: deleteSection },
          {
            icon: 'list',
            method: (sec) => $router.push(`/dashboard/videos/${sec.number}/${sec.stage}`),
          },
        ]"
      />
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  created() {
    this.stage = "6";
  },
  data() {
    return {
      sections: [],
      stage: "",
      loading: false,
    };
  },
  watch: {
    stage(val) {
      this.loading = true;
      axios.get("/getSectionsByStage/" + val).then((res) => {
        this.loading = false;
        this.sections = res.data.sections.sort((a, b) => (a.number < b.number ? -1 : 1));
      });
    },
  },
  methods: {
    deleteSection(section) {
      const id = section._id;
      const confirmed = confirm("مسح الوحدة نهائيا ؟");
      if (!confirmed) return;
      axios.delete("/deleteSection/" + id).then((res) => {
        if (res.data.done) {
          this.sections.splice(
            this.sections.findIndex((sec) => sec._id == id),
            1
          );
        }
      });
    },
  },
};
</script>
